import React, { Component } from 'react';

class ChordInput extends Component {
    constructor(props) {
        super(props);
        this.state = {
            chordName: '',
            showSuggestions: false,
            suggestions: []
        };

        // 扩展和弦音程映射
        this.chordMap = {
            // 大三和弦
            'C': ['1', '3', '5'],
            'D': ['2', '#4', '6'],
            'E': ['3', '#5', '7'],
            'F': ['4', '6', '1'],
            'G': ['5', '7', '2'],
            'A': ['6', '1', '3'],
            'B': ['7', '2', '#4'],
            
            // 小三和弦
            'Cm': ['1', 'b3', '5'],
            'Dm': ['2', '4', '6'],
            'Em': ['3', '5', '7'],
            'Fm': ['4', 'b6', '1'],
            'Gm': ['5', 'b7', '2'],
            'Am': ['6', '1', '3'],
            'Bm': ['7', '2', '4'],

            // 增三和弦
            'Caug': ['1', '3', '#5'],
            'Daug': ['2', '#4', '#6'],
            'Eaug': ['3', '#5', '#7'],
            'Faug': ['4', '6', '#1'],
            'Gaug': ['5', '7', '#2'],
            'Aaug': ['6', '1', '#3'],
            'Baug': ['7', '2', '#4'],

            // 减三和弦
            'Cdim': ['1', 'b3', 'b5'],
            'Ddim': ['2', '4', 'b6'],
            'Edim': ['3', '5', 'b7'],
            'Fdim': ['4', 'b6', 'b1'],
            'Gdim': ['5', 'b7', 'b2'],
            'Adim': ['6', '1', 'b3'],
            'Bdim': ['7', '2', 'b4'],

            // 七和弦
            'C7': ['1', '3', '5', 'b7'],
            'D7': ['2', '#4', '6', '1'],
            'E7': ['3', '#5', '7', '2'],
            'F7': ['4', '6', '1', 'b3'],
            'G7': ['5', '7', '2', '4'],
            'A7': ['6', '1', '3', '5'],
            'B7': ['7', '2', '#4', '6'],

            // 大七和弦
            'Cmaj7': ['1', '3', '5', '7'],
            'Dmaj7': ['2', '#4', '6', '#1'],
            'Emaj7': ['3', '#5', '7', '#2'],
            'Fmaj7': ['4', '6', '1', '3'],
            'Gmaj7': ['5', '7', '2', '#4'],
            'Amaj7': ['6', '1', '3', '#5'],
            'Bmaj7': ['7', '2', '#4', '#6'],

            // 小七和弦
            'Cm7': ['1', 'b3', '5', 'b7'],
            'Dm7': ['2', '4', '6', '1'],
            'Em7': ['3', '5', '7', '2'],
            'Fm7': ['4', 'b6', '1', 'b3'],
            'Gm7': ['5', 'b7', '2', '4'],
            'Am7': ['6', '1', '3', '5'],
            'Bm7': ['7', '2', '4', '6'],

            // 半减七和弦
            'Cm7b5': ['1', 'b3', 'b5', 'b7'],
            'Dm7b5': ['2', '4', 'b6', '1'],
            'Em7b5': ['3', '5', 'b7', '2'],
            'Fm7b5': ['4', 'b6', 'b1', 'b3'],
            'Gm7b5': ['5', 'b7', 'b2', '4'],
            'Am7b5': ['6', '1', 'b3', '5'],
            'Bm7b5': ['7', '2', 'b4', '6'],

            // 减七和弦
            'Cdim7': ['1', 'b3', 'b5', 'bb7'],
            'C#dim7': ['#1', '3', '5', 'b7'],
            'Ddim7': ['2', 'b4', 'b6', 'bb7'],
            'D#dim7': ['#2', '4', '6', 'b7'],
            'Edim7': ['3', 'b5', 'b7', 'bb7'],
            'Fdim7': ['4', 'b6', 'b1', 'bb7'],
            'F#dim7': ['#4', '6', '1', 'b7'],
            'Gdim7': ['5', 'b7', 'b2', 'bb7'],
            'G#dim7': ['#5', '7', '2', 'b7'],
            'Adim7': ['6', 'b1', 'b3', 'bb7'],
            'A#dim7': ['#6', '1', '3', 'b7'],
            'Bdim7': ['7', 'b2', 'b4', 'bb7'],

            // sus4和弦
            'Csus4': ['1', '4', '5'],
            'Dsus4': ['2', '5', '6'],
            'Esus4': ['3', '6', '7'],
            'Fsus4': ['4', '7', '1'],
            'Gsus4': ['5', '1', '2'],
            'Asus4': ['6', '2', '3'],
            'Bsus4': ['7', '3', '#4']
        };

        // 和弦类型分类
        this.chordCategories = {
            '大三和弦': ['C', 'D', 'E', 'F', 'G', 'A', 'B'],
            '小三和弦': ['Cm', 'Dm', 'Em', 'Fm', 'Gm', 'Am', 'Bm'],
            '增三和弦': ['Caug', 'Daug', 'Eaug', 'Faug', 'Gaug', 'Aaug', 'Baug'],
            '减三和弦': ['Cdim', 'Ddim', 'Edim', 'Fdim', 'Gdim', 'Adim', 'Bdim'],
            '属七和弦': ['C7', 'D7', 'E7', 'F7', 'G7', 'A7', 'B7'],
            '大七和弦': ['Cmaj7', 'Dmaj7', 'Emaj7', 'Fmaj7', 'Gmaj7', 'Amaj7', 'Bmaj7'],
            '小七和弦': ['Cm7', 'Dm7', 'Em7', 'Fm7', 'Gm7', 'Am7', 'Bm7'],
            '半减七和弦': ['Cm7b5', 'Dm7b5', 'Em7b5', 'Fm7b5', 'Gm7b5', 'Am7b5', 'Bm7b5'],
            '减七和弦': ['Cdim7', 'C#dim7', 'Ddim7', 'D#dim7', 'Edim7', 'Fdim7', 'F#dim7', 'Gdim7', 'G#dim7', 'Adim7', 'A#dim7', 'Bdim7'],
            'sus4和弦': ['Csus4', 'Dsus4', 'Esus4', 'Fsus4', 'Gsus4', 'Asus4', 'Bsus4']
        };
    }

    selectChord = (category, chord) => {
        this.setState({ 
            chordName: chord,
            showSuggestions: false 
        });
        this.props.onChordChange(this.chordMap[chord], chord);
    }

    handleInputChange = (e) => {
        const value = e.target.value.trim();
        this.setState({ chordName: value });

        // 搜索建议
        if (value) {
            const suggestions = Object.values(this.chordMap)
                .flat()
                .filter(chord => chord.toLowerCase().startsWith(value.toLowerCase()))
                .slice(0, 5);
            this.setState({ 
                suggestions,
                showSuggestions: suggestions.length > 0 
            });
        } else {
            this.setState({ 
                suggestions: [],
                showSuggestions: false 
            });
        }

        // 如果在和弦映射表中找到对应的和弦，就更新和弦音程
        if (this.chordMap[value]) {
            this.props.onChordChange(this.chordMap[value], value);
        }
    }

    render() {
        return (
            <div className="chord-input-container">
                <div className="chord-input-wrapper">
                    <input
                        type="text"
                        value={this.state.chordName}
                        onChange={this.handleInputChange}
                        placeholder="输入和弦名称"
                        className="chord-input"
                    />
                </div>
                <div className="chord-categories">
                    {Object.entries(this.chordCategories).map(([category, chords]) => (
                        <div key={category} className="chord-category">
                            <h3>{category}</h3>
                            <div className="chord-buttons">
                                {chords.map(chord => (
                                    <button
                                        key={chord}
                                        onClick={() => this.selectChord(category, chord)}
                                        className="chord-button"
                                    >
                                        {chord}
                                    </button>
                                ))}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        );
    }
}

export default ChordInput;



