import React, { Component } from 'react';
import './App.css';
import ChordSelect from './component/chordSelect';
import ChordDraw from './component/chordDraw';
import html2canvas from 'html2canvas';

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			savedChords: []
		}
	}

	hideLoading() {
		this.refs.chordSelect.hideLoading();
	}

	selectFinish(chordTone) {
		this.refs.chordDraw.draw(chordTone);
	}

	handleSaveChord = (chord) => {
		this.setState(prevState => ({
			savedChords: [...prevState.savedChords, chord]
		}));
	}

	handleRemoveChord = (chordId) => {
		this.setState(prevState => ({
			savedChords: prevState.savedChords.filter(chord => chord.id !== chordId)
		}));
	}

	clearSavedChords = () => {
		this.setState({ savedChords: [] });
	};

	printSavedChords = () => {
		const savedChordsSection = document.getElementById('saved-chords-section');
		if (!savedChordsSection) return;

		// 临时移除不需要的元素的显示
		const header = savedChordsSection.querySelector('.saved-chords-header');
		const actions = savedChordsSection.querySelector('.saved-chords-actions');
		const removeButtons = savedChordsSection.querySelectorAll('.remove-chord-btn');
		
		if (header) header.style.display = 'none';
		if (actions) actions.style.display = 'none';
		removeButtons.forEach(btn => btn.style.display = 'none');

		// 计算每行可以放置的和弦数量（A4纸宽度约为210mm）
		const chordWidth = 130; // 和弦宽度（像素）
		const chordsPerRow = Math.floor((210 - 20) / (chordWidth / 3.78)); // 3.78是像素到毫米的大致转换比例

		html2canvas(savedChordsSection, {
			backgroundColor: 'white',
			scale: 2, // 提高图片质量
			logging: false,
			onclone: (clonedDoc) => {
				// 在克隆的文档中调整布局
				const clonedHeader = clonedDoc.querySelector('.saved-chords-header');
				const clonedActions = clonedDoc.querySelector('.saved-chords-actions');
				const clonedRemoveButtons = clonedDoc.querySelectorAll('.remove-chord-btn');
				const clonedChordsList = clonedDoc.querySelector('.saved-chords-list');
				
				if (clonedHeader) clonedHeader.style.display = 'none';
				if (clonedActions) clonedActions.style.display = 'none';
				clonedRemoveButtons.forEach(btn => btn.style.display = 'none');

				// 调整和弦列表的布局
				if (clonedChordsList) {
					clonedChordsList.style.display = 'grid';
					clonedChordsList.style.gridTemplateColumns = `repeat(${chordsPerRow}, 1fr)`;
					clonedChordsList.style.gap = '10mm';
					clonedChordsList.style.justifyItems = 'center';
					clonedChordsList.style.padding = '10mm';
				}
			}
		}).then(canvas => {
			// 创建打印窗口
			const printWindow = window.open('', '', 'width=800,height=600');
			const printContent = `
				<html>
					<head>
						<style>
							@page {
								size: A4;
								margin: 0;
							}
							body {
								margin: 0;
								padding: 0;
							}
							img {
								width: 210mm;
								max-height: 297mm;
								object-fit: contain;
								page-break-inside: avoid;
							}
							@media print {
								html, body {
									width: 210mm;
									height: 297mm;
								}
							}
						</style>
					</head>
					<body>
						<img src="${canvas.toDataURL('image/png')}" />
						<script>
							window.onload = function() {
								window.print();
								setTimeout(() => window.close(), 100);
							};
						</script>
					</body>
				</html>
			`;
			
			printWindow.document.write(printContent);
			printWindow.document.close();

			// 恢复原始显示状态
			if (header) header.style.display = '';
			if (actions) actions.style.display = '';
			removeButtons.forEach(btn => btn.style.display = '');
		});
	};

	saveAsImage = () => {
		const savedChordsSection = document.getElementById('saved-chords-section');
		if (!savedChordsSection) return;

		// 临时移除不需要的元素的显示
		const header = savedChordsSection.querySelector('.saved-chords-header');
		const actions = savedChordsSection.querySelector('.saved-chords-actions');
		const removeButtons = savedChordsSection.querySelectorAll('.remove-chord-btn');
		
		if (header) header.style.display = 'none';
		if (actions) actions.style.display = 'none';
		removeButtons.forEach(btn => btn.style.display = 'none');

		html2canvas(savedChordsSection, {
			backgroundColor: 'white',
			scale: 2, // 提高图片质量
			logging: false,
			onclone: (clonedDoc) => {
				// 在克隆的文档中也隐藏这些元素
				const clonedHeader = clonedDoc.querySelector('.saved-chords-header');
				const clonedActions = clonedDoc.querySelector('.saved-chords-actions');
				const clonedRemoveButtons = clonedDoc.querySelectorAll('.remove-chord-btn');
				
				if (clonedHeader) clonedHeader.style.display = 'none';
				if (clonedActions) clonedActions.style.display = 'none';
				clonedRemoveButtons.forEach(btn => btn.style.display = 'none');
			}
		}).then(canvas => {
			// 创建下载链接
			const link = document.createElement('a');
			link.download = 'saved-chords.png';
			link.href = canvas.toDataURL('image/png');
			link.click();

			// 恢复原始显示状态
			if (header) header.style.display = '';
			if (actions) actions.style.display = '';
			removeButtons.forEach(btn => btn.style.display = '');
		});
	};

	render() {
		return (
			<div className="App">
				<ChordSelect ref="chordSelect" selectFinish={this.selectFinish.bind(this)}/>
				<ChordDraw 
					ref="chordDraw" 
					hideLoading={this.hideLoading.bind(this)}
					onSaveChord={this.handleSaveChord}
					onRemoveChord={this.handleRemoveChord}
				/>
				{this.state.savedChords.length > 0 && (
					<div className="saved-chords-container" id="saved-chords-section">
						<div className="saved-chords-header">
							<h2 className="saved-chords-title">已保存的和弦</h2>
							<div className="saved-chords-actions">
								<button className="action-btn print-btn" onClick={this.printSavedChords}>
									打印和弦
								</button>
								<button className="action-btn save-as-image-btn" onClick={this.saveAsImage}>
									保存为图片
								</button>
								<button className="action-btn clear-btn" onClick={this.clearSavedChords}>
									清空和弦
								</button>
							</div>
						</div>
						<div className="saved-chords-list">
							{this.state.savedChords.map(chord => (
								<div key={chord.id} className="saved-chord-item">
									<div 
										className="chord-svg" 
										onClick={() => this.refs.chordDraw.guitarChord.playChord(chord.tones)}
										dangerouslySetInnerHTML={{ __html: chord.svgContent }}
									/>
									<button 
										className="remove-chord-btn"
										onClick={() => this.handleRemoveChord(chord.id)}
									>
										×
									</button>
								</div>
							))}
						</div>
					</div>
				)}
			</div>
		);
	}
}

export default App;


